.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

.wrapper{
  max-width: 1400px;
  margin:0 auto;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans','Open Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background:#A5668B;
}
h1{
  font-family: 'Ubuntu',sans serif;
  font-size: 5rem;
  text-align: center;
  color:#0E103D
}
h2,h3{
  font-family: 'Ubuntu',sans-serif;
  font-size: 2.5rem;
  color:#0E103D
}
p, a, li, input{
  font-family: 'Open Sans',sans-serif;
  font-size: 1.5rem;
  color:#0E103D

}
.explanationSection{
  display: flex;
  justify-content: space-evenly;
  
}
.explanation{
  width:45%;
  background: #F2D7EE;
  border-radius: 25px;
  text-align: center;
}
h3{
  text-align: center;
}
.inputForm{
  display: flex;
  flex-direction: column;
}
.inputContainer{
  display: flex;
  background: #F2D7EE;
  justify-content: space-evenly;
  border-radius:12px;
  width: 15vw;
  margin-top: 5%;
}
input{
  width:20%;
  align-self: center;
  border-radius: 25px;
  background: #F2D7EE;
  height:35px;
  margin-bottom: 20px;
  text-align: center;
}
button{
  width:10%;
  align-self: center;
  border-radius: 25px;
  border:1px solid purple;
  background: #f2d7ee;
  height:40px;
  box-shadow: 2px 2px 2px purple;
}
button:hover, button:focus{
  background: purple;
  cursor: pointer;
}
button:active{
  box-shadow: 0 5px purple;
  transform: translateY(4px);
}
.responseArea{
  display: flex;
  justify-content: space-evenly;
  margin-top: 10%;
  width: 65%;
  padding-left: 13%;
  margin-left: 10%;
  flex-wrap: wrap;
}

.deleteBtn{
  height:20px;
  width:20px;
  padding-left:4px;
}
footer{
  background: #f2d7ee;
  margin-top:50vh;
  text-align: center;
}
footer p{
  margin:0;
}
a:hover, a:focus{
  color:purple;
}
a{
  text-decoration: none;
}

/* Media Queries: */
@media(max-width:1000px){
  input{
    width:30%;
  }
}
@media(max-width:795px){
   button{
    width:20%;
  }
  .inputContainer{
    width:40%;
  }
  input{
    width:40%;
  }
}
@media(max-width:580px){
  h1{
    font-size: 4rem;
  }
  h2,h3{
    font-size: 2rem;
  }
  p,a,li,input{
    font-size: 22px;
  }
  .explanationSection{
    flex-direction: column;
    align-items: center;
  }
  .explanation{
    width:90%;
    height: 35vh;
    margin-top: 5%;
  }
  .responseArea{
    width:90%;
    padding-left: 0;
    margin-left: 5%;
    justify-content: space-around;
  }
  input{
    width:50%
  }
}
@media(max-width:450px){
  .explanation{
    height:40vh;
  }
}
@media(max-width:370px){
  .explanation{
    height:45vh;
  }
  input{
    width:60%;
  }
}


